.list-item {
    display: flex;
    align-items: center;
    height: 40px;
    border-radius: 8px;
    background-color: wheat;
    margin-bottom: 8px;
    padding: 8px;

    &:last-child {
        margin-bottom: 0;
    }

    &_done {
        opacity: 0.7;

        .list-item__text {
            text-decoration: line-through;
        }
    }

    &__checkbox {
        margin-right: 8px;
    }

    &__delete-btn {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        outline: none;
        cursor: pointer;
        background: transparent;
        border: none;
        margin-left: auto;
        text-decoration: none;
        padding: 0;
        transform: rotate(45deg);
        font-size: 18px;
        line-height: 24px;
        text-align: center;
        opacity: 0.7;

        &::before {
            content: "+";
        }
    }
}