.create-task {
    display: flex;
    margin-bottom: 8px;

    &__input {
        height: 32px;
        border: 3px solid #ccc;
        border-radius: 8px;
        flex: 1;
    }
}

.btn {
    height: 32px;
    min-width: 64px;
    border: 3px solid #ccc;
    outline: none;
    cursor: pointer;
    border-radius: 8px;
    margin-left: 16px;
}

